<script>
import functions from "../../util/functions.js";
import constants from "../../util/constants";
import DatePicker from "vue2-datepicker";

export default {
  props: {
    add_mode: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    ...functions, 
    verifierChargement() {
      this.nbr_charges += 1;
      if (this.nbr_charges == this.nbr_total_chargements) {
        this.is_loading_naissances = false;
      }
    },
    async loadNaissance() {
      let url =
        this.constants.api_url +
        "/naissance/" +
        encodeURIComponent(this.$route.params.id_naissance);
      let response = null;
      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        
        response = await fetch(url, {
          method: "get",
          cache: "no-cache",
          headers 
        });
      } catch (err) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: err.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.id) {
        this.naissance = {
            ...data,
            // est_disponible :   data.naissance.est_disponible == 1 ? true : false,
            // est_en_promotion :   data.naissance.est_en_promotion == 1 ? true : false,
            // est_nouveau :   data.naissance.est_nouveau == 1 ? true : false, 
        };
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
    },
    async loadCollections() {
      let url =
        this.constants.api_url +
        "/collections/get.php" ;
      let response = null;
      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        
        response = await fetch(url, {
          method: "get",
          cache: "no-cache",
          headers 
        });
      } catch (err) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: err.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS" && data.collections) {
        this.listCollections = data.collections;
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
    },
    onImageAdded(image) {
      this.naissance.images = [...this.naissance.images, image];
    },
    onImageUpdated(image) {
      let index = this.naissance.images.findIndex(
        (img) => img.id_image == image.id_image
      );
      if (index >= 0) {
        this.naissance.images[index] = image;
      }
    },
    onImageDeleted(image) {
      this.naissance.images = this.naissance.images.filter(
        (img) => img.id_image != image.id_image
      );
    },
    verifierNaissance(naissance) {
      let message = "";
      if (naissance.nom == undefined || naissance.nom == "") {
        message = "Veuillez entrer un nom correcte svp";
      } else if (naissance.postnom == undefined || naissance.postnom == "") {
        message = "Veuillez entrer un postnom correcte svp";
      }  else if (naissance.prenom == undefined || naissance.prenom == "") {
        message = "Veuillez entrer un prénom correcte svp";
      }  else if (naissance.sexe == undefined || naissance.sexe == "") {
        message = "Veuillez sélectionner un sexe svp";
      }  else if (naissance.date_naissance == undefined || naissance.date_naissance == "") {
        message = "Veuillez entrer une date de naissance correcte";
      }   
      
      if (message != "") {
        this.$swal({
          type: "warning",
          title: "Les données fournies sont incorrcetes",
          text: message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      return {
        status: message === "",
        message,
      };
    },
    async doAddNaissance() {
      if (!this.verifierNaissance(this.naissance).status) return;

      this.loading_message = "Enregistrement du decès  en cours";
      this.is_doing_action = true;
      let url = this.constants.api_url + "/naissance/";
      let response = null;

      try {
        let obj = {
          ...this.naissance,
          user_id  : this.utilisateur.id
        }
        delete obj.id;

        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        response = await fetch(url, {
          method: "post",
          cache: "no-cache",
          headers  : {
            "Content-Type" : "application/json"
          },
          body:   JSON.stringify({
            ...obj
          }), 
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.success) {
         
        this.naissance = this.default_naissance; 

        this.$snotify.success("La naissance a été enregistré avec succès.", {
          timeout: 1500,
          showProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          
        }); 
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
    async doEditNaissance() {
      if (!this.verifierNaissance(this.naissance).status) return;

      this.loading_message = "Enregistrement   en cours";
      this.is_doing_action = true;
      let url = this.constants.api_url + "/naissance/" + this.naissance.id;
      let response = null;

      try {
        let obj = {
          ...this.naissance,
          user_id  : this.utilisateur.id
        }
        delete obj.id;

        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        response = await fetch(url, {
          method: "put",
          cache: "no-cache",
          headers  : {
            "Content-Type" : "application/json"
          },
          body:   JSON.stringify({
            ...obj
          }), 
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.success) {
         
        // this.naissance = this.default_naissance; 

        this.$snotify.success("La naissance a été modifiée avec succès.", {
          timeout: 1500,
          showProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          
        });  

        this.$router.push("/admin/naissances/liste-all-naissances")

      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
   
    async doUpdateProfilImage(id_naissance) {
      if (this.profile_image_changed && this.ifile) {
        this.loading_message = "Mise à jour de l'image en cours";
        let url =
          this.constants.api_url + "/naissances/change-image.php?id_naissance=" + id_naissance;

        const formData = new FormData();
        formData.append("img", this.ifile, this.ifile.name);

        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        let response = await fetch(url, {
          method: "post",
          body: formData,
          credentials: "include",
          cache: "no-cache",
          // headers,
        });

        let data = await response.json();
        if (data.status == "SUCCESS") {
          return {
            status: "SUCCESS",
          };
        } else {
          return data;
        }
      } else {
        return { status: "NO_CHANGE" };
      }
    },
    onProfilImageChange(event) {
      let file = event.target.files[0];
      this.filename = "";
      this.preview_img = null; //defaultPreviewImg;
      this.preview_img_base64 = "";
      this.ifile = null;
      if (!file) return;
      let reader = new FileReader();
      reader.onload = () => {
        this.img_profil = reader.result;
        let base64 = reader.result.substr(
          reader.result.indexOf(",") + 1,
          reader.result.length - (reader.result.indexOf(",") + 1)
        );
        this.filename = file.name;
        this.ifile = file;
        this.preview_img = this.img_profil;
        this.preview_img_base64 = base64;

        this.profile_image_changed = true;
        // setImagesChanged(true);
      };
      reader.readAsDataURL(file);
    },
    deleteImage() {
      this.filename = "";
      this.preview_img = null; //defaultPreviewImg;
      this.preview_img_base64 = "";
      this.ifile = null;
      this.profile_image_changed = true;
    },
    getImgObjModel(
      id_naissance,
      nom_fichier = "",
      id_image = -1,
      description = ""
    ) {
      return {
        description,
        id_naissance: id_naissance,
        code_contexte: "IMG_JOUEUR",
        nom_fichier,
        id_image,
      };
    },
  },
  async created() {
    if (!this.add_mode) {
     this.nbr_total_chargements += 1;
      this.is_loading_naissances = true;
      this.loadNaissance().then(() => this.verifierChargement()); 
    }
    try {
      this.utilisateur = JSON.parse(sessionStorage.getItem('utilisateur'));
    } catch(e) {
      this.utilisateur = {id : -1}
    }

  },
  data() {
    return {
      constants,

      nbr_total_chargements: 0,
      nbr_charges: 0,

      sortBy: " ",
      perPage: 10,
      currentPage: 1,
      sortDesc: false,
      sortByFormatted: true,
      filterByFormatted: true,
      sortable: true,
      mouvements_naissance: [],
      fields: [
        { key: "date_mouvement", sortable: true },
        { key: "etat_naissance", sortable: false },
        { key: "observation", sortable: true },
        // { key: "action", sortable: true },
      ],

      is_loading_naissances: false,
      is_doing_action: false,
      listCollections : [],
      naissance: { 
        id: -1,
        nom : "",
        postnom : "",
        prenom : "",
        sexe : "",
        date_naissance : "", 
          
        nom_pere : "",
        lieu_naissance_pere : "",
        date_naissance_pere : "",
        proffesion_pere : "",
        nationalite_pere : "",
        
        nom_mere : "",
        lieu_naissance_mere : "",
        date_naissance_mere : "",
        proffesion_mere : "",
        nationalite_mere : "",
      },
      default_naissance: { 
        id: -1,
        nom : "",
        postnom : "",
        prenom : "",
        sexe : "",
        pointage_naiss_normal : "",
        pointage_naiss_pediatrie : "",
          
        nom_pere : "",
        lieu_naissance_pere : "",
        date_naissance_pere : "",
        proffesion_pere : "",
        nationalite_pere : "",
        
        nom_mere : "",
        lieu_naissance_mere : "",
        date_naissance_mere : "",
        proffesion_mere : "",
        nationalite_mere : "",
      },
      utilisateur : {},

      loading_message: "Chargement de données en cours",

      // sur la preview de l'image de profil
      filename: "",
      preview_img: null,
      preview_img_base64: "",
      ifile: null,
      img_profil: null,
      profile_image_changed: false,
    };
  },
  components: { 
    DatePicker
  },
};
</script>

<template>
  <section class="news-grid mt-5">
    <div class="page-header">
      <h3 class="page-title">
        {{
          add_mode ? "Enregistrer une naissance" : "Editer les informations sur une Article"
        }}
      </h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <router-link :to="{ name: 'dashboard' }"
              >Saisie de données</router-link
            >
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            {{
              add_mode
                ? "Enregistrer une naissance"
                : "Editer les informations sur un Article"
            }}
          </li>
        </ol>
      </nav>
    </div>

    <div class="mt-5 pt-4" v-if="is_loading_naissances">
      <div class="card">
        <div class="card-body">
          <div class="circle-loader"></div>
          <p class="text-center mt-4">Chargement de données en cours</p>
        </div>
      </div>
    </div>
    <div class="row mt-5" v-else-if="!is_loading_naissances">
      <div class="col-md-12">
        <div class="card mb-3" v-if="is_doing_action">
          <div class="card-body">
            <div class="circle-loader"></div>
            <p class="text-center mt-4">{{ loading_message }}</p>
          </div>
        </div>

        <div class="card mb-4">
          <div class="card-body">   
            <div class="row">
            <div class="col-md-7">
              <fieldset class="mt-4" :disabled="is_doing_action">
                <b-form-group label="Nom  :" label-for="nom">
                    <b-form-input
                    type="text"
                    class="mb-3"
                    id="nom"
                    v-model="naissance.nom"
                    placeholder="Nom"
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Postnom  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.postnom"
                    placeholder="Postnom "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Prénom  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.prenom"
                    placeholder="Postnom "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Sexe :" label-for="sexe_defun">
                    <select id="sexe" v-model="naissance.sexe" class="form-control">
                        <option   :value="'Masculin'">Masculin</option>
                        <option   :value="'Feminin'">Féminin</option>
                    </select>
                </b-form-group>


                <b-form-group
                  label="Date de naissance   :"
                  label-for="date_naissance_"
                >
                  <date-picker
                    v-model="naissance.date_naissance"
                    valueType="format"
                    placeholder="Date de naissance  "
                  ></date-picker>
                </b-form-group>

 
                <!-- <b-form-group label="Pointage naissance normale :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.pointage_naiss_normal"
                    placeholder="Pointage naissance normale "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Pointage naissance pédiatrie :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.pointage_naiss_pediatrie"
                    placeholder="Pointage naissance pédiatrie "
                    ></b-form-input>
                </b-form-group> -->


                <!-- Infos sur le pere -->
                <b-form-group label="Nom du père  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.nom_pere"
                    placeholder="Nom du père "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Lieu de naissance du père  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.lieu_naissance_pere"
                    placeholder="Lieu de naissance du père "
                    ></b-form-input>
                </b-form-group>

                <b-form-group
                  label="Date de naissance du pere  :"
                  label-for="date_naissance"
                >
                  <date-picker
                    v-model="naissance.date_naissance_pere"
                    valueType="format"
                    placeholder="Date de naissance du père "
                  ></date-picker>
                </b-form-group>

                <b-form-group label="Profession du père  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.proffesion_pere"
                    placeholder="Profession du père "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Nationalité du père  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.nationalite_pere"
                    placeholder="Nationalité du père "
                    ></b-form-input>
                </b-form-group>




                <!-- Infos sur la mere -->
                <b-form-group label="Nom de la mère  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.nom_mere"
                    placeholder="Nom de la mère "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Lieu de naissance de la mère  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.lieu_naissance_mere"
                    placeholder="Lieu de naissance de la mère "
                    ></b-form-input>
                </b-form-group>

                <b-form-group
                  label="Date de naissance de la mère  :"
                  label-for="date_naissance"
                >
                  <date-picker
                    v-model="naissance.date_naissance_mere"
                    valueType="format"
                    placeholder="Date de naissance de la mère "
                  ></date-picker>
                </b-form-group>

                <b-form-group label="Profession de la mère  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.proffesion_mere"
                    placeholder="Profession de la mère "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Nationalité de la mère  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="naissance.nationalite_mere"
                    placeholder="Nationalité de la mère "
                    ></b-form-input>
                </b-form-group>



                <b-button
                    type="submit"
                    :disabled="is_doing_action"
                    variant="success"
                    class="mr-2 mt-4"
                    @click="add_mode ? doAddNaissance() : doEditNaissance()"
                    >{{
                    add_mode
                        ? "Enregistrer la naissance"
                        : "Enregistrer les modifications apportées"
                    }}</b-button
                >
                </fieldset>
            </div>

             
            </div>  
          </div>
        </div>
      </div>
    </div>
 
  </section>
</template>
<style scoped>
.detail-naissance-item {
  font-size: 1.08em;
}
.biographie-text {
  font-size: 1.1em;
}
</style>